<template>
  <div class="col-md-12">
    <div class="card card-container">

      <Form @submit="handleLogin" >
        <div class="form-group">
          <label for="username">Username</label>
          <input name="username" v-model="username"  type="text" class="form-control" />
          <span name="username" class="error-feedback" />
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input name="password" v-model="password"  type="password" class="form-control" />
          <span name="password" class="error-feedback" />
        </div>

        <div class="form-group">
          <button class="btn btn-primary btn-block" :disabled="loading">
            <span
                v-show="loading"
                class="spinner-border spinner-border-sm"
            ></span>
            <span>Login</span>
          </button>
        </div>

        <div class="form-group">
          <div v-if="message" class="alert alert-danger" role="alert">
            {{ message }}
          </div>
        </div>
      </Form>
    </div>
  </div>
</template>

<script>




import {string} from "yup";

export default {
  name: "Login",

  components: {
  },
  data() {


    return {
      loading: false,
      message: "",
      username:"",
      password:""
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/dashboard/default");
    }
  },
  methods: {
    handleLogin(e) {
      e.preventDefault()
      this.loading = true

      this.$store.dispatch("auth/login",{ username: this.username,password:this.password}).then(
          () => {
            this.$router.push("/dashboard/default")
          },
          (error) => {
            this.loading = false;
            this.message =
                (error.response &&
                    error.response.data &&
                    error.response.data.message) ||
                error.message ||
                error.toString();
          }
      );
    },
  },
};
</script>

<style scoped>

</style>